<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="客户端id" :required="true">
            <el-input v-model.number="requestParam.app_id"></el-input>
          </el-form-item>
          <!-- <el-form-item label="BossId">
            <el-input v-model="requestParam.boss_id"></el-input>
          </el-form-item> -->
          <el-form-item label="渠道码" :required="true">
            <el-input v-model="requestParam.channel_code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getTurntableConfig"
              >查询</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="form_container">
        <el-form
          :label-position="labelPosition"
          label-width="100px"
          :model="responseData.turntableConfig"
          class="formStyle text_common"
          size="mini"
          ref="turntableConfig"
        >
          <el-form-item label="操作">
            <el-button
              type="primary"
              @click="updateTable"
              v-show="action == '' && !noData"
              >修改</el-button
            >
            <el-button
              type="success"
              @click="createTable"
              v-if="!(action == 'edit')"
              >新增</el-button
            >
            <el-button
              type="success"
              @click="addDay"
              :disabled="see_action"
              v-show="!(action == '')"
              >新增一天</el-button
            >
            <el-button
              type="primary"
              v-show="action === 'edit' || action === 'add'"
              @click="onSubmit()"
              v-text="action === 'add' ? '确认新增' : '确认修改'"
            ></el-button>
          </el-form-item>
          <el-form-item
            label="AppId"
            prop="app_id"
            v-show="!noData || action != ''"
          >
            <el-input
              v-model.number="responseData.turntableConfig.app_id"
              style="width:830px"
              :disabled="see_action"
            ></el-input>
          </el-form-item>
          <!-- <el-form-item
            label="BossId"
            prop="boss_id"
            v-show="!noData || action != ''"
          >
            <el-input
              v-model.number="responseData.turntableConfig.boss_id"
              style="width:830px"
              :disabled="see_action"
            ></el-input>
          </el-form-item> -->
          <el-form-item
            label="渠道码"
            prop="channel_code"
            v-show="!noData || action != ''"
          >
            <el-input
              v-model="responseData.turntableConfig.channel_code"
              placeholder="可不传,传要确定正确!!!"
              style="width:830px"
              :disabled="see_action"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="抽奖间隔(秒)"
            prop="interval"
            v-show="!noData || action != ''"
          >
            <el-input
              v-model.number="responseData.turntableConfig.interval"
              style="width:830px"
              :disabled="see_action"
            ></el-input>
          </el-form-item>
          <el-form-item prop="cfgval">
            <div
              v-for="(item, index) in responseData.turntableConfig.cfgval"
              :key="index"
              class="every_day"
            >
              <div class="excuteDiv">
                <el-button
                  type="success"
                  plain
                  @click="addPart(index)"
                  :disabled="see_action"
                  v-show="action != ''"
                  >新增一个奖品</el-button
                >
                <el-button
                  type="warning"
                  plain
                  @click="removePart(index)"
                  :disabled="see_action"
                  v-show="action != ''"
                  >移除一个奖品</el-button
                >
                <el-button
                  type="danger"
                  plain
                  @click="removeDay(index)"
                  :disabled="see_action"
                  v-show="action != ''"
                  >移除该天配置</el-button
                >
                <span v-show="false">{{ index + 1 }}</span>
              </div>
              <el-table
                :data="item"
                style="width:71%"
                border
                :header-cell-style="tableHeaderStyle"
              >
                <el-table-column
                  prop="index"
                  label="排序"
                  width="105"
                  type="index"
                  highlight-current-row
                >
                </el-table-column>
                <el-table-column
                  prop="type"
                  :v-model="item[index]"
                  label="物品类型"
                  width="180"
                >
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.type"
                      :disabled="see_action"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="id"
                  label="物品Id"
                  :v-model="item[index]"
                  width="180"
                >
                  <template slot-scope="scope">
                    <el-input
                      v-model="scope.row.id"
                      :disabled="see_action"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="num"
                  label="物品数量"
                  :v-model="item[index]"
                  width="180"
                >
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.num"
                      :disabled="see_action"
                    ></el-input>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="random"
                  label="获取概率"
                  :v-model="item[index]"
                  width="180"
                >
                  <template slot-scope="scope">
                    <el-input
                      v-model.number="scope.row.random"
                      :disabled="see_action"
                    ></el-input>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      tableHeaderStyle: {
        "background-color": "#F1F1F1"
      },
      requestParam: {
        app_id: "",
       // boss_id: "",
        channel_code: ""
      },
      responseData: {
        turntableConfig: {
          app_id: "",
          //boss_id: "",
          channel_code: "",
          interval: "", // 每次抽奖的间隔时间
          cfgval: [
            [
              // {
              //   type: "",
              //   id: "",
              //   num: "",
              //   random: ""
              // }
            ]
          ] // 配置数据
        }
      },
      see_action: true,
      action: "",
      noData: true
    };
  },
  methods: {
    getTurntableConfig() {
      if (this.requestParam.app_id == ""||this.requestParam.channel_code=='') {
        this.$message({
          showClose: true,
          message: "AppId和渠道码不能为空!",
          type: "error"
        });
        return;
      }
      var that = this;
      this.$api.activity.turntableConfig(this.requestParam).then(res => {
        if (res.data.success === true) {
          if (res.data.turntableConfig == null) {
            this.responseData.turntableConfig = {
              app_id: "",
              // boss_id: "",
              channel_code: "",
              interval: "",
              cfgval: [[]]
            };
            that.noData = true;
          } else {
            that.noData = false;
            that.responseData.turntableConfig.app_id = that.requestParam.app_id;
           // that.responseData.turntableConfig.boss_id = that.requestParam.boss_id;
            that.responseData.turntableConfig.channel_code = that.requestParam.channel_code;
            that.responseData.turntableConfig.interval = res.data.turntableConfig.interval;
            that.responseData.turntableConfig.cfgval = res.data.turntableConfig.cfgval;
          }
        } else {
          this.$message({
            showClose: true,
            message: "获取转盘配置失败",
            type: "error"
          });
        }
      });
    },
    updateTable() {
      this.see_action = false;
      this.action = "edit";
    },
    resetForm() {
      this.responseData.turntableConfig = {
        app_id: "",
       // boss_id: "",
        channel_code: "",
        interval: "",
        cfgval: [[]]
      };
    },
    createTable() {
      if (
        this.responseData.turntableConfig != null &&
        this.responseData.turntableConfig != "undefined" &&
        this.responseData.turntableConfig.cfgval.length > 0
      ) {
        this.$confirm("新增可能会丢失之前的配置,确定要新增吗？", "新增").then(
          action => {
            this.see_action = false;
            this.action = "add";
            this.resetForm();
          }
        );
      }
    },
    addDay() {
      let arr = [
        {
          type: 0,
          id: "",
          num: 0,
          random: 0
        }
      ];
      this.responseData.turntableConfig.cfgval.push(arr);
    },
    removeDay(index) {
      this.responseData.turntableConfig.cfgval.splice(index, 1);
    },
    addPart(dayIndex) {
      this.responseData.turntableConfig.cfgval[dayIndex].push({
        type: 0,
        id: "",
        num: 0,
        random: 0
      });
    },
    removePart(dayIndex) {
      this.responseData.turntableConfig.cfgval[dayIndex].splice(
        this.responseData.turntableConfig.cfgval[dayIndex].length - 1,
        1
      );
    },
    onSubmit() {
      var checkRandom = true;
      this.responseData.turntableConfig.cfgval.forEach(item => {
        var countRandom = 0;
        item.forEach(item2 => {
          countRandom += parseInt(item2.random);
        });
        if (countRandom != 100) {
          checkRandom = false;
        }
      });
      if (
        this.responseData.turntableConfig.interval === "" ||
        checkRandom == false
      ) {
        this.$message({
          showClose: true,
          message:
            checkRandom == false
              ? "每组配置概念之和必须为100"
              : "时间间隔不能设置为空",
          type: "error"
        });
        return false;
      }
      var that = this;
      for (
        var i = 0;
        i < this.responseData.turntableConfig.cfgval.length;
        i++
      ) {
        for (
          var j = 0;
          j < this.responseData.turntableConfig.cfgval[i].length;
          j++
        ) {
          this.responseData.turntableConfig.cfgval[i][j].type = parseInt(
            this.responseData.turntableConfig.cfgval[i][j].type
          );
          this.responseData.turntableConfig.cfgval[i][j].num = parseInt(
            this.responseData.turntableConfig.cfgval[i][j].num
          );
          this.responseData.turntableConfig.cfgval[i][j].random = parseInt(
            this.responseData.turntableConfig.cfgval[i][j].random
          );
        }
      }
      this.$api.activity
        .setTurntableConfig(this.responseData.turntableConfig)
        .then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "新增游戏成功",
              type: "success"
            });
            that.action = "";
            that.see_action = true;
            that.noData = false;
          } else {
            this.$message({
              showClose: true,
              message: "设置转盘配置失败",
              type: "error"
            });
          }
        });
    }
  }
  // ,
  // mounted() {
  //   this.getTurntableConfig();
  // }
};
</script>

<style lang="less" scoped>
.core_container {
  width: 88%;
  height: 100%;
  float: right;
  // background-color: cadetblue;
  margin: auto;
  overflow-y: auto;
}
.form_container {
  // border: #eef1f6 2px solid;
  width: 100%;
  // margin: 2% 5%;
  // box-shadow: 0px 3px 2px #eef1f6;
}
.el-form {
  border: #eef1f6 2px solid;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.formStyle {
  width: 75%;
  margin: auto auto;
  padding: 60px 0;
}
.text_common {
  color: #888;
  font-size: 13px;
}
.every_day {
  margin-top: 20px;
}
.every_part {
  text-align: center;
  .el-input {
    width: 100px;
    margin-right: 20px;
  }
}
h2 {
  text-align: center;
}
.excuteDiv {
  display: flex;
  justify-content: flex-start;
}
</style>



